import React, {Component} from 'react';
import picture from '../kitchen.jpg';

class Kitchen extends Component {
    render() {
        return (
            <div>
                <div className="container">
                    <div className="row">
                        <div className="col-5">
                            <img src={picture} alt=""/>
                        </div>
                        <div className="col-7">
                            <h3></h3>

                            <p> -        .     ,       . </p>
                            <p>  ,        . -,      .  ,      -      .</p> <p>-,    -,       .         ,      ,        .</p>
                            <br/>
                            <h5>      ? </h5>
                            <br/>
                            <p>  <i>   ,      ,   .
                                <br/> <b>  -  . ,            . </b></i> </p>
                            {this.props.filtered.map( (item, index)=>
                                <div key={item.index}>
                                    <span>{item.desc}</span>
                                    <input type="checkbox" name="subscribe" value="newsletter"></input>

                                </div>
                                )}
                        </div>

                    </div>
                </div>
            </div>
        );
    }
}

export default Kitchen;


