import React, {Component} from 'react';
import { BrowserRouter, Route, NavLink } from 'react-router-dom';
import './Navigation.css'
import Routes from "../../Routes";


const Navigation = (props) => {

    return (
        <BrowserRouter>
            <div>
                <h1></h1>
                <div className="navigation_sphearsTabs flex">
                    <div className="sphearsPanel"></div>
                    <NavLink className="sphearsTab" exact to="/"></NavLink>
                    <NavLink className="sphearsTab" to="/career"></NavLink>
                    <NavLink className="sphearsTab" to="/relationships"></NavLink>
                    <NavLink className="sphearsTab" to="/finances"></NavLink>
                    <NavLink className="sphearsTab" to="/health"></NavLink>
                    <NavLink className="sphearsTab" to="/rest">/</NavLink>
                    <NavLink className="sphearsTab" to="/personal-growth"> </NavLink>
                    <NavLink className="sphearsTab" to="/physical-environment/">. </NavLink>
                    <NavLink className="sphearsTab" to="/persona"></NavLink>
                </div>
                <Routes/>
            </div>
        </BrowserRouter>
    );
};

export default Navigation;


