import React, {Component} from 'react';
import Testing_Start from "../../stages/testing/_start/Testing_Start";
import picture from '../environment/_start/physical_environment.jpg';
import Start from "../../markup/_start/Start";
const quote = {
    text:  " ,    ,      .",
    author: " "
};


class Carrer extends Component {
    textDesc()  {
        return (
            <div>
                <p>
                     ,   ,     , ,    .   , ,   ,   .   ,    ,          ,     ,         .</p>

                <p>    <b>        . </b>    ,       .    -           .
                          ,         ,      .      .
                </p>
                <p>         .        ,       .
                </p>

            </div>
        )
    };

    render() {
        return (
            <Start path="career" name=" " picture = {picture} quote={quote} textDesc = {this.textDesc()}/>
        )
    }
}

export default Carrer;


